export const DRAMAMANWAY_TEMPLATE = new RegExp(
    `\\[    ] #(?<index>\\d\\d?)\\s*

(?<titleRu>.+) [\\|\\/] (?<titleEng>.+) [\\|\\/] (?<titleOriginal>.+)

(((?<country>.+) - )?(?<year>\\d{4})  - (?<episodesNumber>\\d+).+

)?(===   ===:
(?<aboutSection>.+)
===   ===:
(?<ideaSection>.+)
===  ===:
(?<feedbackSection>.+)
===   \\? ===:
(?<negativeAspectsSection>.+)
===  ===:
(?<casteSection>.+)
===  ===:
\\*  - (?<plot>\\d\\d?)  10(?<plotComment>.*)
(\\*  - (?<dialogues>\\d\\d?)  10(?<dialoguesComment>.*)
)?\\*   - (?<idea>\\d\\d?)  10(?<ideaComment>.*)
(\\*  - (?<soundtrack>\\d\\d?)  10(?<soundtrackComment>.*)
)?\\* \\( \\) - (?<sufficiency>\\d\\d?)  10(?<sufficiencyComment>.*)
\\*  - (?<caste>\\d\\d?)  10(?<casteComment>.*)
\\* ,    - (?<rewatchingChance>\\d\\d?)  10(?<rewatchingChanceComment>.*)
-----
\\*  - (?<cliche>\\d\\d?)  10(?<clicheComment>.*)
\\*  - (?<stupidity>\\d\\d?)  10(?<stupidityComment>.*)
\\*  - (?<tightness>\\d\\d?)  10(?<tightnessComment>.*)
-----
\\*  : (?<finalScore>\\d\\d?)  10(?<finalScoreComment>.*)

===    ===:
(?<recommendationSection>.+)

)?===   \\"(?<nextPost>.+)\\"`
        .replaceAll(' ', '\\s+')
        .replace(/^/gm, '\\s*')
        .replaceAll('.+', '.+?') // non-greedy
        .replaceAll('.*', '.*?') // non-greedy
        .replaceAll('\n', '<br>'),
    'is'
);
